export const getRandomIntInclusive = (min, max) => {
  min = Math.ceil(min)
  max = Math.floor(max)
  return Math.floor(Math.random() * (max - min + 1)) + min // 
}
export const formatTime = (time) => {
  if (isNaN(time)) return ''
  // 2.()
  const differSecond = time
  // 3. /  = 
  let day = Math.floor(differSecond / (60 * 60 * 24))
  day = day >= 10 ? day : '0' + day
  // 4. /  % 24;
  let hour = Math.floor(differSecond / (60 * 60) % 24)
  hour = hour >= 10 ? hour : '0' + hour
  // 5. /  % 60;
  let minute = Math.floor(differSecond / 60 % 60)
  minute = minute >= 10 ? minute : '0' + minute
  // 6. % 
  let second = Math.floor(differSecond % 60)
  second = second >= 10 ? second : '0' + second
  return {
    day: day,
    hour: hour,
    minute: minute,
    second: second
  }
}
export const setLocalStorage = (key, value) => {
  window.localStorage.setItem(key, JSON.stringify(value))
}
export const getLocalStorage = (key) => {
  return JSON.parse(window.localStorage.getItem(key))
}
// A-Z
export const getLetterChars = () => {
  const result = []
  for (let i = 65; i < 91; i++) {
    const char = String.fromCharCode(i)
    result.push(char)
  }
  return result
}

// 
export function parseLyric(lrc) {
  const lyrics = lrc.split('\n')
  // [00:00.000]  : 
  // 1.[00:00.000]
  const reg1 = /\[\d*:\d*\.\d*\]/g
  // 2. [00
  const reg2 = /\[\d*/i
  // 3. :00
  const reg3 = /\:\d*/i
  // 4.
  const lyricObj = {}
  lyrics.forEach(function(lyric) {
    // 1.
    let timeStr = lyric.match(reg1)
    if (!timeStr) {
      return
    }
    timeStr = timeStr[0]
    // 2.
    const minStr = timeStr.match(reg2)[0].substr(1)
    // 3.
    const secondStr = timeStr.match(reg3)[0].substr(1)
    // 4., 
    const time = parseInt(minStr) * 60 + parseInt(secondStr)
    // 5.
    const text = lyric.replace(reg1, '').trim()
    // 6.
    lyricObj[time] = text
  })
  return lyricObj
}
